"use client"

import Link from "next/link"
import { Card } from "@/components/ui/card"

interface NewsItem {
  id: number
  category: "고교입시" | "마이스터고" | "특성화고"
  title: string
  summary: string
  date: string
  link: string
}

const newsItems: NewsItem[] = [
  {
    id: 1,
    category: "고교입시",
    title: "2025학년도 고교 입시 일정 및 주요 변경사항 안내",
    summary: "2025학년도 고등학교 입시 일정과 주요 변경사항을 확인하세요. 학생부 종합전형, 특성화고 전형 등 다양한 전형 방법을 소개합니다.",
    date: "2025.01.15",
    link: "#",
  },
  {
    id: 2,
    category: "마이스터고",
    title: "마이스터고 신입생 모집 및 입학 전형 요강 공개",
    summary: "전국 마이스터고등학교의 2025학년도 신입생 모집 일정과 입학 전형 요강이 공개되었습니다. 실기고사와 면접을 포함한 전형 방법을 확인하세요.",
    date: "2025.01.12",
    link: "#",
  },
  {
    id: 3,
    category: "특성화고",
    title: "특성화고 취업률 상승 및 산업체 연계 프로그램 확대",
    summary: "특성화고등학교의 취업률이 지속적으로 상승하고 있으며, 산업체와의 연계 프로그램이 확대되고 있습니다.",
    date: "2025.01.10",
    link: "#",
  },
  {
    id: 4,
    category: "고교입시",
    title: "학생부 종합전형 평가 기준 및 준비 방법",
    summary: "학생부 종합전형의 평가 기준과 효과적인 준비 방법을 전문가가 조언합니다. 내신 관리와 비교과 활동의 균형을 맞추는 것이 중요합니다.",
    date: "2025.01.08",
    link: "#",
  },
  {
    id: 5,
    category: "마이스터고",
    title: "마이스터고 졸업생 취업 현황 및 진로 경로",
    summary: "마이스터고 졸업생들의 우수한 취업 현황과 다양한 진로 경로를 소개합니다. 전문 기술 인력으로서의 성장 가능성을 확인하세요.",
    date: "2025.01.05",
    link: "#",
  },
  {
    id: 6,
    category: "특성화고",
    title: "특성화고 교육과정 개편 및 NCS 기반 교육 강화",
    summary: "특성화고등학교의 교육과정이 개편되며 NCS 기반 실무 교육이 강화됩니다. 산업 현장에서 요구하는 역량을 기를 수 있는 교육 환경을 제공합니다.",
    date: "2025.01.03",
    link: "#",
  },
]

const categoryColors = {
  고교입시: "bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200",
  마이스터고: "bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200",
  특성화고: "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200",
}

export default function News() {
  return (
    <section className="py-16 lg:py-24 bg-muted/30">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-3xl lg:text-4xl font-bold text-foreground mb-4">
            최신 뉴스
          </h2>
          <p className="text-lg text-muted-foreground">
            고교입시, 마이스터고, 특성화고 관련 최신 소식을 확인하세요
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {newsItems.map((item) => (
            <Card key={item.id} className="p-6 hover:shadow-lg transition-shadow">
              <div className="flex items-center justify-between mb-3">
                <span
                  className={`px-3 py-1 rounded-full text-xs font-medium ${categoryColors[item.category]}`}
                >
                  {item.category}
                </span>
                <span className="text-xs text-muted-foreground">{item.date}</span>
              </div>
              <h3 className="text-lg font-bold text-foreground mb-2 line-clamp-2">
                {item.title}
              </h3>
              <p className="text-sm text-muted-foreground mb-4 line-clamp-3">
                {item.summary}
              </p>
              <Link
                href={item.link}
                className="text-sm text-primary hover:underline font-medium"
              >
                자세히 보기 →
              </Link>
            </Card>
          ))}
        </div>

        <div className="text-center mt-12">
          <Link
            href="#"
            className="inline-flex items-center px-6 py-3 border-2 border-primary text-primary rounded-full font-medium hover:bg-primary hover:text-primary-foreground transition-all"
          >
            더 많은 뉴스 보기
          </Link>
        </div>
      </div>
    </section>
  )
}


